html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
  margin: 0px;
}
body{
  background-color: #f2f2f2;
}
#root {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-family: 'Nunito', sans-serif;
  max-height: 100vh;
}

#root.dark {
    background-color: #1c1c1c;
}




footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 1200px;
    padding: 30px 0px 20px 900px;
    position: fixed;
    bottom: 30px;
}

footer a{
    display: block;
    width: 60px;
    height: 60px;
    border-radius: 88px;
    background: linear-gradient(145deg, #ffffff, #dadada);
    box-shadow:  8px 8px 16px #cecece,
                 -8px -8px 16px #ffffff;
    padding: 15px;
    cursor:pointer;
}

footer a:hover {
    background: linear-gradient(145deg, #dadada, #ffffff);
    box-shadow:  8px 8px 16px #cecece,
                 -8px -8px 16px #ffffff;
}

footer a.dark {
    background: linear-gradient(145deg, #1e1e1e, #191919);
}

footer a.dark:hover{
    background: linear-gradient(145deg, #1e1e1e, #191919);
}
header {
    margin-top: 40px;
    padding-left: 20px;
    border-radius: 14px;
    background: var(--main-background-color);
    position:-webkit-sticky;
    position:sticky;
    width: 1200px;
    height: 100px;
    top: 20px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    border-radius: 11px;
    background: #f2f2f2;
    box-shadow:  15px 15px 31px #cecece,
             -15px -15px 31px #ffffff;
             
    text-shadow: 1px 1px 5px rgb(255, 255, 204);
}

header h1{
    color:  var(--main-font-color);
    text-shadow: 1px 1px 5px rgb(255, 255, 204);
}

header.dark{
    background: #1c1c1c;
    box-shadow:  30px 30px 71px #0d0d0d,
             -30px -30px 71px #2b2b2b;
             color: #cecece;
             
    text-shadow: 1px 1px 5px rgb(245, 131, 131);
}

.screen {
    width: 890px;
    margin-top: 20px;
    height: calc(100% - 20px);
    background-color: white;
    border-radius: 11px;
    background: #f2f2f2;
    box-shadow: inset 5px 5px 10px #dfdfdf,
        inset -5px -5px 10px #ffffff;
    float: left;
    overflow-y: scroll;
    
}

.screen.dark {
    background: #1c1c1c;
    box-shadow: inset 15px 15px 31px #0d0d0d,
            inset -15px -15px 31px #2b2b2b;
}

#about {
    height: 1000px;
    padding: 50px;
}

article {
    margin-bottom: 100px;
}

h3{
    text-align: center;
    font-size: 24px;
    margin-bottom: 20px;
}

p{
    text-indent: 50px;
    font-size: 20px;
    width: 60%;
    display: inline-block;
    word-wrap:break-word;
    margin-top: 50px;
}

#selfie{
    width: 300px;
    float: right;
    border-radius: 18px;
    box-shadow:  23px 23px 45px #cecece,
                -23px -23px 45px #ffffff;
                margin-bottom: 50px;
}

#skills{
}

.skills_container{
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
}
.portfolio_container {
    padding: 30px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    grid-gap: 30px;
    gap: 30px;
    height: auto;
    padding-bottom: 100px;
}

.portfolio_category_container {
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    grid-gap: 20px;
    gap: 20px;
    margin: 40px auto 20px;
    justify-content: space-evenly;
}

.portfolio_menu_item summary {
    text-align: center;
    padding-bottom: 10px;
    padding: 20px;
    border-radius: 9px;
    background: #f2f2f2;
    box-shadow: 19px 19px 37px #cecece,
        -19px -19px 37px #ffffff;
    font-size: larger;
}

.portfolio_submenu_item{
    text-align: center;
    width: 100%;
    text-indent:0;
    margin-top: 30px;
    font-weight: lighter;
}
.portfolioCard {
    height: 250px;
    width: 250px;
    background-color: #fff;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
    box-shadow: 5px 5px 10px #e1e1e1,
        -5px -5px 10px #ffffff;
    border-radius: 7px;
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
}

.portfolioCard img {
    width: 90%;
    border: 5px double #f2f2f2;
    border-radius: 5px;
}

.portfolioCard a {
    text-decoration: none;
    cursor: pointer;
    color: black;
}

.portfolioCard:hover {
    transition: -webkit-transform 1s;
    transition: transform 1s;
    transition: transform 1s, -webkit-transform 1s;
    -webkit-transform: scale3d(1.05, 1.05, 1.05);
    transform: scale3d(1.05, 1.05, 1.05);
}

