}
main {
    width: 1200px;
    height: calc(100vh - 190px);
}
aside {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-evenly;
}

aside form{
    display: flex;
    flex-direction: column;
    padding-left: 15px;
    padding-top: 40px;
}

aside label{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 180px;
    height: 50px;
    margin-bottom: 10px;
    border-radius: 5px;
    border: 1px #dddddd solid;
    background: #f2f2f2;
    box-shadow:  20px 20px 60px #cecece,
    -20px -20px 60px #ffffff;
}

aside label:hover{
    background: linear-gradient(145deg, #dadada, #ffffff);
    box-shadow:  20px 20px 60px #cecece,
             -20px -20px 60px #ffffff;
}

aside input {
    visibility: hidden;
}
