footer{
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    width: 1200px;
    padding: 30px 0px 20px 900px;
    position: fixed;
    bottom: 30px;
}

footer a{
    display: block;
    width: 60px;
    height: 60px;
    border-radius: 88px;
    background: linear-gradient(145deg, #ffffff, #dadada);
    box-shadow:  8px 8px 16px #cecece,
                 -8px -8px 16px #ffffff;
    padding: 15px;
    cursor:pointer;
}

footer a:hover {
    background: linear-gradient(145deg, #dadada, #ffffff);
    box-shadow:  8px 8px 16px #cecece,
                 -8px -8px 16px #ffffff;
}

footer a.dark {
    background: linear-gradient(145deg, #1e1e1e, #191919);
}

footer a.dark:hover{
    background: linear-gradient(145deg, #1e1e1e, #191919);
}